interface ValidationResponse {
    received: object,
    errors: string[]
};

export class Validator {
    static isValid(item: object, mandatory_attributes: string[], optional_attributes: string[]): ValidationResponse {
        let validated_attributes = [];
        const item_attributes = Object.keys(item);
        let errors = [];
        
        for (let mandatory_attribute of mandatory_attributes) {
            let found = false;

            for (let attribute of item_attributes) {
                if (attribute == mandatory_attribute) {
                    validated_attributes.push(attribute);
                    found = true;

                    break;
                }
            }

            if (!found) {
                errors.push("The '" + mandatory_attribute + "' attribute is missing");
            }
        }

        return {
            received: item,
            errors: errors
        };
    }
};