import { APIGatewayProxyEvent, APIGatewayProxyResult } from 'aws-lambda';
const { CognitoIdentityClient, CreateIdentityPoolCommand } = require("@aws-sdk/client-cognito-identity");

import { Validator } from '../utils/Validator';
import user_CRUD_model from '../utils/crud-models/userCRUDModel';

const { userPoolId, region } = process.env;
const cognito = new CognitoIdentityClient({ region: region });
const { mandatory_attributes, optional_attributes } = user_CRUD_model.create;

export const handler = async (event: APIGatewayProxyEvent): Promise<APIGatewayProxyResult> => {
    let response: APIGatewayProxyResult;

    const body = JSON.parse(event.body ? event.body : "");

    const { received, errors } = Validator.isValid(body, mandatory_attributes, optional_attributes);

    if (errors.length > 0) {
        return {
            statusCode: 400,
            headers: {
                "Content-Type": "application/json",
                "Access-Control-Allow-Origin": "*"
            },
            body: JSON.stringify({
                message: "Wrong data format received",
                received,
                errors
            })
        };
    }

    try {
        const command = new CreateIdentityPoolCommand({
            UserPoolId: userPoolId,
            Username: body.email,
            UserAttributes: [{
                Name: 'email',
                Value: body.email
            },
            {
                Name: 'email_verified',
                Value: 'true'
            }
            ],
            MessageAction: 'SUPPRESS'
        });
        
        const creation_response = await cognito.send(command);
        
        response = {
            statusCode: 201,
            headers: {
                "Content-Type": "application/json",
                "Access-Control-Allow-Origin": "*"
            },
            body: JSON.stringify({
                message: "User created"
            })
        };
    }
    catch (err) {
        console.log(err);
        
        response = {
            statusCode: 500,
            body: JSON.stringify({
                message: 'Something went wrong',
                received
            })
        };
    }

    return response;
};
